import ServiceScope from '../../serviceScope/ServiceScope';
import MockFetchProvider, { MockedHeader } from '../../httpClient/test/MockFetchProvider';
import { fetchProviderServiceKey } from '../../httpClient/FetchProvider';
import MockRandomProvider from '../../httpClient/test/MockRandomProvider';
import { randomProviderServiceKey } from '../../common/RandomProvider';
import PageContext, { IPageContextData, pageContextServiceKey }  from '../../pageContext/PageContext';
import SPPermission from '../../permission/SPPermission';
import OAuthTokenProvider, { oAuthTokenProviderServiceKey } from '../OAuthTokenProvider';
import { IOAuthToken, OAuthUtilityError, OAuthUtilityErrorType } from '@ms/sp-client-shared';

const assert: Chai.AssertStatic = chai.assert;

interface IFetchProviderExpectations {
  requestAssertions: (request: Request) => void;
  responseObject: any; // tslint:disable-line no-any
  responseOptions?: ResponseInit;
}

describe('OAuthTokenProvider tests', () => {
  let testCase: number = 0;
  const rootScope: ServiceScope = ServiceScope.startNewRoot();
  const fetchProvider: MockFetchProvider = new MockFetchProvider(rootScope);
  rootScope.provide(fetchProviderServiceKey, fetchProvider);
  const randomProvider: MockRandomProvider = new MockRandomProvider(rootScope);
  rootScope.provide(randomProviderServiceKey, randomProvider);
  const pageContextProvider: PageContext = new PageContext(rootScope);
  const pageContextInfo: IPageContextData = {
    cultureInfo: {
      currentCultureName: '',
      currentUICultureName: '',
      isRightToLeft: false
    },
    site: {
      id: ''
    },
    user: {
      displayName: '',
      email: '',
      loginName: ''
    },
    web: {
      absoluteUrl: '',
      id: '',
      permissions: SPPermission.emptyMask.value,
      serverRelativeUrl: '/sites/test/',
      title: ''
    }
  };
  pageContextProvider.initialize(pageContextInfo);
  rootScope.provide(pageContextServiceKey, pageContextProvider);
  rootScope.finish();
  const tokenProvider: OAuthTokenProvider = rootScope.consume(oAuthTokenProviderServiceKey);

  beforeEach(() => {
    testCase++;
  });

  it('Should get the correct token in case of a successfull request', () => {
    fetchProvider.expect({
      expectedUrl: '/sites/test/_api/contextinfo',
      responseObject: {
        'FormDigestValue': 'DIGEST_1',
        'FormDigestTimeoutSeconds': 30
      }
    });

    setFetchProviderExpectation({
      requestAssertions: (request: Request): void => {
        assert.isTrue(request.headers.has('X-RequestDigest'));
      },
      responseObject: {
        access_token: 'test_token',
        expires_on: Date.now() / 1000 + 10000,
        not_before: '1467752142',
        resource: 'resource',
        scope: 'user_impersonation',
        token_type: 'Bearer'
      }
    });

    return tokenProvider.getOAuthToken(getTestResource()).then((oAuthToken: IOAuthToken) => {
      assert.equal(oAuthToken.token, 'test_token');
    });
  });

  it('should get the right error in case of an erroneous request', () => {
    setFetchProviderExpectation({
      requestAssertions: (request: Request): void => {
        assert.isTrue(request.headers.has('X-RequestDigest'));
      },
      responseObject: {
        error: {
          message: 'error',
          code: '1, some.exception'
        }
      },
      responseOptions: {
        status: 400
      }
    });

    return tokenProvider.getOAuthToken(getTestResource()).then(() => { assert.fail(); },
      (error: any) => {// tslint:disable-line no-any
        chai.expect(error).instanceof(OAuthUtilityError);
        const tokenUtilError: OAuthUtilityError = error as OAuthUtilityError;
        assert.equal(tokenUtilError.type, OAuthUtilityErrorType.serverError);
        assert.equal(tokenUtilError.serverErrorCode, 1);
        assert.equal(tokenUtilError.message, 'error');
      });
  });

  /* tslint:disable no-any */
  function setFetchProviderExpectation(expectations: IFetchProviderExpectations): void {
  /* tslint:enable no-any */
    fetchProvider.expect({
      expectedUrl: '/sites/test/_api/SP.OAuth.Token/Acquire',
      expectedRequestHeaders: [
        new MockedHeader('Content-Type', 'application/json; charset=utf-8'),
        new MockedHeader('Odata-Version', '4.0'),
        new MockedHeader('Accept', 'application/json;odata.metadata=minimal')
      ],
      requestAssertions: expectations.requestAssertions,
      responseObject: expectations.responseObject,
      responseOptions: expectations.responseOptions
    });
  }

  function getTestResource(): string {
    return 'resource' + testCase;
  }
});